import { Helmet } from 'react-helmet';
import { Route, Switch, useHistory, Redirect, useLocation } from 'react-router-dom';
import { useEffect, useState } from 'react';
import {CurrentUserContext} from "../contexts/CurrentUserContext";
import mainApi from '../utils/MainApi';
import useEscapePress from '../hooks/useEscapePress';
import Header from './Header';
import Main from './Main';
import Footer from './Footer';
import Movies from './Movies';
import SavedMovies from './SavedMovies';
import Register from './Register';
import Login from './Login';
import Profile from './Profile';
import NotFound from './NotFound';
import ProtectedRoute from './ProtectedRoute';

function App() {
  const history = useHistory();
  const [isBurgerMenuOpened, setIsBurgerMenuOpened] = useState(false);
  const [savedMovies, setSavedMovies] = useState([]);
  const location = useLocation();
  const [currentUser, setCurrentUser] = useState({});
  const [loggedIn, setLoggedIn] = useState(false);

  const headerEndpoints = ['/', '/movies', '/saved-movies', '/profile'];
  const footerEndpoints = ['/', '/movies', '/saved-movies'];

  const onClickBurgerMenu = (isBurgerMenuOpened) => {
    setIsBurgerMenuOpened(!isBurgerMenuOpened);
  }

  const returnOnPrevPage = () => {
    history.goBack();
  }

  function handleRegister({ name, email, password }) {
    mainApi
      .register(name, email, password)
      .then(data => {
        if(data._id) {
          handleLogin({ email, password });
        }
      })
      .catch(error =>
        console.log(error)
      );
  }

  function handleLogin({ email, password }) {
    mainApi
      .login(email, password)
      .then(jwt => {
        if(jwt.token) {
          setLoggedIn(true);
          localStorage.setItem('jwt', jwt.token);
          history.push('/movies');
        }
      })
      .catch(error => {
        console.log(error);
      })
  }

  function handleSignOut() {
    localStorage.removeItem('jwt');
    setLoggedIn(false);
    history.push('/');
    setCurrentUser({});
    setLoggedIn(false);
  }

  function handleEditProfile({ name, email }) {
    mainApi
      .setUserInfo(name, email)
      .then(newData => {
        setCurrentUser(newData);
      })
      .catch(error => {
        console.log(error);
      })
  }

  function handleSaveMovie(movie) {
    mainApi
      .addMovieInSaves(movie)
      .then(data => setSavedMovies([data, ...savedMovies]))
      .catch(error => {
        console.log(error);
      });
  }

  function handleDeleteMovie(movie) {
    const savedMovie = savedMovies.find(
      (item) => item.movieId === movie.id ||
        item.movieId === movie.movieId
    );
    mainApi
      .deleteSavedMovie(savedMovie._id)
      .then(() => {
        const newMovies = savedMovies.filter(item => {
          if (item.movieId === movie.id || item.movieId === movie.movieId)
            return false;
          else
            return true;
        });
        setSavedMovies(newMovies)
      })
      .catch(error => {
        console.log(error);
      });
  }

  useEscapePress(onClickBurgerMenu, isBurgerMenuOpened);

  useEffect(() => {
    const path = location.pathname;
    const jwt = localStorage.getItem('jwt');
    if (jwt) {
      mainApi
        .getUserInfo()
        .then(data => {
          if (data) {
            setCurrentUser(data);
            setLoggedIn(true);
            history.push(path);
          }
        })
        .catch(error => {
          console.log(error);
        })
    } else {
      console.log("")
    }
  }, [history]);

   //   
   useEffect(() => {
    if (loggedIn) {
      mainApi
        .getUserInfo()
        .then(res => setCurrentUser(res))
        .catch(error => {
          console.log(error);
        })
    }
  }, [loggedIn]);

  //    
  useEffect(() => {
    if (loggedIn && currentUser) {
      mainApi
        .getSavedMovies()
        .then(data => {
          const UserMovies = data.filter(movie => movie.owner === currentUser._id);
          setSavedMovies(UserMovies);
        })
        .catch(error => {
          console.log(error);
        });
    }
  }, [currentUser, loggedIn]);

  return (
    <div className='page'>
      <Helmet htmlAttributes={{ lang : "ru" }}/>
      <CurrentUserContext.Provider value={currentUser}>
        <Route exact path = {headerEndpoints}>
          <Header
            onClickBurgerBtn={onClickBurgerMenu}
            isBurgerMenuOpened={isBurgerMenuOpened}
            authorized={loggedIn}
          />
        </Route>

        <Switch>
          <Route exact path="/" >
            <Main />
          </Route >

          <Route exact path="/signup">
            {!loggedIn ? (
              <Register handleRegister={handleRegister}/>
            ) : (
              <Redirect to='/' />
            )}
          </Route>

          <Route exact path="/signin">
          {!loggedIn ? (
            <Login handleLogin={handleLogin}/>
          ) : (
            <Redirect to='/' />
          )}
          </Route>

          <ProtectedRoute
            path="/movies"
            loggedIn={loggedIn}
            savedMovies={savedMovies}
            onLikeClick={handleSaveMovie}
            onDeleteClick={handleDeleteMovie}
            component={Movies}
          />

          <ProtectedRoute
            path="/saved-movies"
            loggedIn={loggedIn}
            savedMovies={savedMovies}
            onDeleteClick={handleDeleteMovie}
            component={SavedMovies}
          />

          <ProtectedRoute
            path='/profile'
            loggedIn={loggedIn}
            handleEditProfile={handleEditProfile}
            handleSignOut={handleSignOut}
            component={Profile}
          />
          <Route path="*">
            <NotFound goBack={returnOnPrevPage} />
          </Route>
        </Switch>
        <Route exact path={footerEndpoints}>
          <Footer />
        </Route>
      </CurrentUserContext.Provider>
    </div>
  );
}

export default App;
